import VueRouter from 'vue-router'
// 引入组件
import TodoList from '../pages/TodoList.vue'
import MyCalendar from '../pages/MyCalendar.vue'
import MyAbout from '../pages/MyAbout.vue'

// 创建并暴露一个路由器
export default new VueRouter({
	routes:[
		{
			path:'/todo',
			component:TodoList
		},
		{
			path:'/cal',
			component:MyCalendar
		},
		{
			path:'/about',
			component:MyAbout
		},
		{
			path:'*',
         redirect:'/todo'
		},

	]
})